
/* ====================================== Add Plant Page CSS ====================================== */
.add-plant-page {
    text-align: left;
    color: #385250;
  }
  
  .add-plant-container {
    width: 1000px;
    height: 700px;
    margin: 0px auto;
    margin-top: -60px;
    position: relative;
    background-color: #FFF7E9;
    padding: 30px;
    border-radius: 30px;
    text-align: center;
    box-shadow: 5px 5px 10px grey;
  }
  
  .upload-image-container {
    width: 50%;
    height: 90%;
    float: left;
  }
  
  .upload-text-container {
    width: 50%;
    height: 90%;
    float: left;
    text-align: left;
    padding: 30px;
  }
  
  .image-drop-box {
    background-color: #E4D9C4;
    margin: 0px auto;
    position: relative;
    width: 90%;
    height: auto;
    border-radius: 16px;
    padding: 10px 0px;
  }
  
  .dotted-box {
    margin: auto;
    width: 95%;
    height: 556px;
    outline: 3px dashed #B0A58F;
    border-radius: 16px;
  }
  
  .upload-button-container {
    position: absolute;
    margin-top: 45%;
    margin-left: 35%;
    z-index: 9; 
  }
  
  #upload-icon {
    height: 40px;
    width: 40px;
    border-radius: 30px;
    color: #E4D9C4;
    position: relative;
  }
  
  #user-uploaded-image {
    z-index: 10;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 16px;
  }
  
  #plant-name {
    margin-top: 50px;
    padding-left: 0px;
    height: 30px;
    width: 400px;
    background-color: #FFF7E9;
    border-radius: 0px;
    border-top: transparent;
    border-left: transparent;
    border-right: transparent;
    outline: none;
    font-family: bold;
    font-size: 2rem;
  }
  
  #plant-name::placeholder {
    color: #385250;
  }
  
  #plant-species {
    padding-left: 0px;
    height: 30px;
    width: 400px;
    background-color: #FFF7E9;
    border-radius: 0px;
    border-top: transparent;
    border-left: transparent;
    border-right: transparent;
    outline: none;
    font-family: 'Josefin Sans';
    font-weight: bold;
    font-size: 1.5rem;
  }
  
  #plant-species::placeholder {
    color: #385250;
  }
  
  #plant-description {
    border: none;
    height: 290px;
    box-shadow: 5px 5px 10px grey;
    font-family: 'Josefin Sans';
    font-weight: bold;
    font-size: 1.5rem;
  }
  
  #plant-description::placeholder {
    color: #385250;
  }
  
  #save-button {
    width: 25%;
  }
  /* ========================================================================================= */
  
  /* ============================== Plant Page CSS ========================================== */
  .plant-container {
    width: 1000px;
    height: 700px;
    margin: 0px auto;
    margin-top: -60px;
    position: relative;
    background-color: #FFF7E9;
    border-radius: 30px;
    text-align: center;
    box-shadow: 5px 5px 10px grey;
  }
  
  .plant-image-container {
    width: 50%;
    height: 100%;
    float: left;
    border-radius: 30px 0px 0px 30px;
  }
  
  .plant-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 30px 0px 0px 30px;
  }
  
  .plant-bio-container {
    width: 50%;
    height: 100%;
    float: left;
    border-radius: 0px 30px 30px 0px;
    padding: 20px;
    text-align: left;
  }
  
  .single-plant-header {
    margin-top: 60px;
    margin-bottom: 20px;
    color: #385250;
    font-family: bold;
  }
  
  #single-plant-description {
    color: #385250;
    font-family: 'Josefin Sans';
    font-size: 1.2rem;
  }
  /* ========================================================================================= */
  
  
  /* ================================== PLANTBASE CSS ===================================== */
  .plantbase-page {
    text-align: center;
    color: #385250;
  }
  
  #back-icon {
    background-color: transparent;
    color: #D1C3A7;
  }
  
  #your-plantbase {
    font-family: bold;
  }
  
  .plantbase-button {
    display: inline-block;
  }
  
  .plantbase-button > p {
    font-size: .8rem;
  }
  
  .plantbase-icon {
    margin: 10px 10px;
    background-color: #385250;
    color: white;
    width: 60px;
    height: 60px;
    border-radius: 10px;
    border: none;
    font-size: 2em;
    justify-content: center;
    cursor: pointer;
  }
  
  #number-of-plants {
    margin-left: 160px;
    margin-bottom: 20px;
    font-family: bold;
  }
  
  .plantbase {
    text-align: left;
    width: 100%;
    height: 100%;
    margin-top: 15px;
  }
  
  .plantbase-container {
    column-count: 5;
    column-gap: 10px;
    height: 100%;
    max-width: 1260;
    padding: 0px 120px
  }
  
  @media (max-width: 859px) {
    .plantbase-container {
      column-count: 1;
    }
  }
  
  @media (min-width: 860px) and (max-width: 1139px) {
    .plantbase-container {
      column-count: 2;
    }
  }
  
  @media (min-width: 1140px) and (max-width: 1439px) {
    .plantbase-container {
      column-count: 3;
    }
  }
  
  @media (min-width: 1440px) and (max-width: 1740px) {
    .plantbase-container {
      column-count: 4;
    }
  }
  /* ========================================================================================= */


    /* ================================== PIN CSS ===================================== */
  .pin {
    width: 300px;
    border-radius: 16px;
    overflow: hidden;
    margin-bottom: 10px;
    position: relative;
    cursor: pointer;
  }

  .pin a:Link {
    text-decoration: none;
  }
  
  .pin .main-pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .pin:hover .pin-plant-name {
    opacity: 1;
    visibility: visible;
  }

  .pin:hover .plant-pin-name-container {
    opacity: 1;
    visibility: visible;
  }

  .pin .plant-pin-name-container {
    background-color: #385250;
    width: 280px;
    height: 50px;
    border-radius: 16px;
    z-index: 10;
    position: absolute;
    bottom: 10px;
    left: 10px;
    opacity: 0;
    visibility: hidden;
    text-align: center;

    transition: opacity .2s, visiblity .2s;
  }

  .pin .pin-plant-name {
    color: white;
    font-family: bold;
    z-index: 10;
    opacity: 0;
    visibility: hidden;
    margin-top: 12px;

    transition: opacity .2s, visiblity .2s;
  }
  /* ========================================================================================= */